<style type="text/css" scoped>
.header {

  background: rgba(7, 17, 27, .5);
  color: #fff;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
}

.content-wrapper {
  align-items: center;
  display: flex;
  padding: 0px 12px 0px 24px;
  position: relative;
}

.avatar {
  flex: 0 0 64px;
  margin-right: 16px;
  width: 64px;
}

.avatar img {
  border-radius: 2px;
  width: 64px;
  height: 64px;
}

.content {
  flex: 1
}

.title {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.brand {
  background-image: url('../../assets/ele/brand@3x.aa6d1a02.png');
  background-repeat: no-repeat;
  background-size: 30px 18px;
  height: 18px;
  width: 30px;
}

.name {
  font-size: 16px;
  font-weight: 700;
  margin-left: 6px;
}

.description {
  margin-bottom: 8px;
  line-height: 12px;
  font-size: 12px;
}

.support {
  display: flex;
  align-items: center;
}

.text {
  font-size: 10px;
  line-height: 12px;
}

.support-count {
  align-items: center;
  background: rgba(7, 17, 27, .2);
  border-radius: 14px;
  bottom: 14px;
  display: flex;
  height: 24px;
  line-height: 24px;
  padding: 0 8px;
  position: absolute;
  right: 12px;
  text-align: center;
}

.count {
  font-size: 10px;
}


.icon-keyboard_arrow_right {

  width: 12px;
  height: 12px;
  margin-left: 3px;
}

.bulletin-wrapper {
  align-items: center;
  background: rgba(7, 17, 27, .2);
  display: flex;
  height: 28px;
  line-height: 28px;
  padding: 0 8px;
  position: relative;
}

.bulletin-title {
  background-image: url('../../assets/ele/bulletin.png');
  background-repeat: no-repeat;
  background-size: 22px 12px;
  flex: 0 0 22px;
  height: 12px;
  margin-right: 4px;
  width: 22px;
}

.bulletin-text {
  flex: 1;
  font-size: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.bulletin-wrapper .icon-keyboard_arrow_right {
  flex: 0 0 10px;
  font-size: 10px;
  width: 10px;
}

.background {
  filter: blur(10px);
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -1;
}

</style>
<template>
  <div class="header" @click="showDetail">
    <div class="content-wrapper">
      <div class="avatar">
        <img :src="seller.avatar">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div v-if="seller.supports" class="support">
          <support-ico :size=1 :type="seller.supports[0].type"></support-ico>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div v-if="seller.supports" class="support-count">
        <span class="count">{{seller.supports.length}}个</span>
        <!-- <span class="icon-keyboard_arrow_right"></span> -->
        <img class="icon-keyboard_arrow_right" src="../../assets/ele/rightArrow.png">
      </div>
    </div>
    <div class="bulletin-wrapper">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <img class="icon-keyboard_arrow_right" src="../../assets/ele/rightArrow.png">
    </div>
    <div class="background">
      <img :src="seller.avatar" width="100%" height="100%">
    </div>
  </div>
</template>
<script>
import SupportIco from './support-ico'
export default {
  props: {
    seller: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  methods: {
    showDetail() {
      this.headerDetailComp = this.headerDetailComp || this.$createHeaderDetail({
        $props: {
          seller: 'seller'
        }
      })
      this.headerDetailComp.show()
    }
  },
  components: {

    SupportIco

  }
}

</script>



