<style type="text/css" scoped>
.tab {
  display: flex;
  flex-direction: column;
  /*height: 100%;*/
  font-size: 14px;
  height: calc(100vh - 165px);

}

.cube-tab-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
}

.cube-tab {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  padding: 10px 0;

}

.cube-tab_active {

  color: #f01414;
  border-bottom: 1px solid #f01414;

}

.slide-wrapper {
  flex: 1;
  overflow: hidden;
}

.goods-content {
  display: flex;
  flex-direction: row;
  height: calc(100% - 207px);
  overflow: hidden;
}

.left-content {
  overflow: hidden;
  width: 90px;
  flex-shrink: 0;
}

.menu-bar-items {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  
}

.menu-bar-item {
  align-items: center;
  background: #f3f5f7;
  display: flex;
  font-size: 12px;
  height: 56px;
  line-height: 14px;
  padding: 0 10px;
}

.menu-bar-item-active {
  background: #fff;
  color: #333;
}

.menu-bar-item-text {
  display: flex;
  flex: 1;
  position: relative;
}

.right-content {
  overflow: hidden;
  height: 100%;
  position: relative;
  width: 100%;
}

.right-content-panels {}

.right-content-panel {}

.panel-title {
  background: #f3f5f7;
  border-left: 2px solid #d9dde1;
  color: #666;
  font-size: 12px;
  height: 26px;
  line-height: 26px;
  padding-left: 14px;
}

.food-item {
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  position: relative;
}

.image-header {


  flex: 0 0 57px;
  margin-right: 10px;

}

.image-header img {
  width: 57px;
  height: 57px;
}

.content {
  flex: 1;
}

.title {

  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #333;
  margin: 2px 0 8px;
}

.detail {
  /*margin-bottom: 18px;*/
  line-height: 10px;
  height: 10px;
  color: #999;
  font-size: 10px;
}

.sell-count {}

.rating {
  margin-right: 12px
}

.price {
  font-weight: 700;
  line-height: 24px;
}

.now {
  color: #f01414;
  font-size: 14px;
  margin-right: 8px;
}

</style>
<template>
  <div class="tab">
    <div class="cube-tab-bar border-bottom-1px">
      <div class="cube-tab cube-tab_active">
        <div>商品</div>
      </div>
      <div class="cube-tab">
        <div>评论</div>
      </div>
      <div class="cube-tab">
        <div>商家</div>
      </div>
      <!-- <div class="cube-tab-bar-slider" style="width: 0px; transform: translateX(0px) translateZ(0px);"></div> -->
    </div>
    <!-- 商品  -->
    <div class="goods-content">
      <div class="left-content" ref="goodsmenu">
        <div class="menu-bar-items">
          <div class="menu-bar-item" :class="{'menu-bar-item-active':currentIndex==index}" v-for="(item ,index) in goods" :key="index" @click="menuItemClick(index)">
            <div class="menu-bar-item-text">
              <support-ico v-if="item.type>=1" :size=1 :type="item.type"></support-ico>
              <span>{{item.name}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="right-content" ref="goodsList">
        <div class="right-content-panels">
          <div ref="rItem" class="right-content-panel" v-for="(panel ,index) in goods" :key="index">
            <div class="panel-title" >
              {{panel.name}}
            </div>
            <div class="food-item" v-for="(food ,index) in panel.foods" :key="index">
              <div class="image-header">
                <img :src="food.image">
              </div>
              <div class="content">
                <h1 class="title">{{food.name}}</h1>
                <div class="detail">
                  <span class="sell-count">月售{{food.sellCount}}份</span>
                  <span class="rating">好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span>
                  <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import goods from '../../../mock/goods.json'
import SupportIco from './support-ico'
import BScroll from 'better-scroll'
export default {
  name: 'tab',
  props: {
    tabs: {
      type: Array,
      default () {
        return []
      }
    },
    initialIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      goods: [],
      foodsScroll: null,
      tops: [],
      scrollY:0
    }
  },
  components: {

    SupportIco

  },
  computed: {

    selectedLabel: {
      get() {
        return this.tabs[this.index].label
      },
      set(newVal) {
        this.index = this.tabs.findIndex((value) => {
          return value.label === newVal
        })
      }
    },
    currentIndex() {
      // 得到条件数据
      const { scrollY, tops } = this
      // 根据条件计算得到一个结果
      const curIndex = tops.findIndex((top, index) => {

        return scrollY >= top && scrollY < tops[index + 1]
      })

      console.log('curIndex==' + curIndex)

      return curIndex
    }
  },
  mounted() {
    this.goods = goods.data
    this.$nextTick(() => {
      this.initScroll()
      this.initTops()
    })
  },


  methods: {
    menuItemClick: function(index) {

      const scrollY = this.tops[index]
      this.scrollY = scrollY

      // 平滑滚动右侧列表
      this.foodsScroll.scrollTo(0, -scrollY, 300)

    },
    initScroll() {

      new BScroll(this.$refs.goodsmenu, {
        click: true
      })
      this.foodsScroll = new BScroll(this.$refs.goodsList, {
        probeType: 3, //探针的效果，实时获取滚动高度
        click: true
      })
      //rights这个对象监听事件，实时获取位置pos.y
      this.foodsScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(pos.y)
      })
    },
    initTops() {

      let rightItems = this.$refs.rItem
      let top = 0
      const tops = []

      tops.push(top)

      for (var i = 0; i < rightItems.length; i++) {
        let item = rightItems[i]
        top += item.clientHeight
        tops.push(top)
      }

      this.tops = tops
      console.log(this.tops)


    }
  }
}

</script>



