<template>
  <span class="support-ico" :class="iconCls"></span>
</template>
<script>
export default {
  name: 'support-ico',
  props: {
    size: {
      type: Number
    },
    type: {
      type: Number
    }
  },
  computed: {
    iconCls() {
      const classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
      return `icon-${this.size} ${classMap[this.type]}`
    }
  }
}

</script>
<style lang="less" scoped>
.support-ico {
  display: inline-block;
  background-repeat: no-repeat;
  margin-right: 4px;

}

.icon-1 {
  width: 12px;
  height: 12px;
  background-size: 12px 12px;
  flex-shrink: 0;

}


.decrease {

  background-image: url('../../assets/ele/decrease.png');
}

.discount {

  background-image: url('../../assets/ele/discount.png');
}

.guarantee {

  background-image: url('../../assets/ele/guarantee.png');
}

.invoice {

  background-image: url('../../assets/ele/invoice.png');
}

.special {

  background-image: url('../../assets/ele/special.png');
}

.icon-2 {
  width: 16px;
  height: 16px;
  background-size: 16px 16px;

  .decrease {

    background-image: url('../../assets/ele/decrease.png');
  }

  .discount {

    background-image: url('../../assets/ele/discount.png');
  }

  .guarantee {

    background-image: url('../../assets/ele/guarantee.png');
  }

  .invoice {

    background-image: url('../../assets/ele/invoice.png');
  }

  .special {

    background-image: url('../../assets/ele/special.png');
  }
}

.icon-3 {
  width: 12px;
  height: 12px;
  background-size: 12px 12px;

  .decrease {

    background-image: url('../../assets/ele/decrease.png');
  }

  .discount {

    background-image: url('../../assets/ele/discount.png');
  }

  .guarantee {

    background-image: url('../../assets/ele/guarantee.png');
  }

  .invoice {

    background-image: url('../../assets/ele/invoice.png');
  }

  .special {

    background-image: url('../../assets/ele/special.png');
  }
}

.icon-4 {
  width: 16px;
  height: 16px;
  background-size: 16px 16px;

  .decrease {

    background-image: url('../../assets/ele/decrease.png');
  }

  .discount {

    background-image: url('../../assets/ele/discount.png');
  }

  .guarantee {

    background-image: url('../../assets/ele/guarantee.png');
  }

  .invoice {

    background-image: url('../../assets/ele/invoice.png');
  }

  .special {

    background-image: url('../../assets/ele/special.png');
  }
}

</style>



